import { Component, OnInit } from '@angular/core';
import {DataApiService} from '../../services/data-api.service';
import { PostInterface } from '../../interface/post';
import {ActivatedRoute} from '@angular/router';
import { BloggersService} from '../../services/bloggers.service';
import { BloggerInterface } from 'src/app/interface/blogger';

@Component({
  selector: 'app-post-detils',
  templateUrl: './post-detils.component.html',
  styleUrls: ['./post-detils.component.css']
})
export class PostDetilsComponent implements OnInit {
  public post: PostInterface = { };
  public blogger: BloggerInterface;

  constructor(private dataApi: DataApiService, private route: ActivatedRoute, private bloggerService: BloggersService) { }

  ngOnInit() {
    const idPost = this.route.snapshot.params['id'];
    this.getDetails(idPost);
  }

  getDetails(idPost: string) {
    this.dataApi.getOnePost(idPost).subscribe(post => {
      console.log('Detaljni POST', post);
      this.post = post;
      this.getBloggerDetail(post.userUID);
  });
}
getBloggerDetail(blogerID: string) {
  this.bloggerService.getOneBlogger(blogerID).subscribe(blogger => {
    this.blogger = blogger;
    console.log(blogger);
  });
}
}
