import { Component, OnInit } from '@angular/core';
import { DataApiService } from 'src/app/services/data-api.service';
import {PostInterface} from '../../interface/post';
import {NgForm} from '@angular/forms';
import {AngularFireAuth} from '@angular/fire/auth';
import {AuthService} from '../../services/auth.service';
import {BloggerInterface} from '../../interface/blogger';

@Component({
  selector: 'app-list-posts',
  templateUrl: './list-posts.component.html',
  styleUrls: ['./list-posts.component.css']
})
export class ListPostsComponent implements OnInit {

  constructor(private dataApi: DataApiService, private authService: AuthService) { }
  posts: PostInterface  [];
  public isAdmin: any = null;
  public userUID: string = null;
  ngOnInit() {
    this.getListPost();
    this.getCurrentBlogger();
  }
  getCurrentBlogger() {
    this.authService.isAuth().subscribe(auth => {
      console.log('auth', auth);
      if ( auth ) {
        console.log(this.isAdmin);
        this.userUID = auth.uid;
        this.authService.isUserAdmin(this.userUID).subscribe(userRole => {
         this.isAdmin = Object.assign({}, userRole.roles).hasOwnProperty('admin');
          });
        }});
  }

  getListPost() {
    this.dataApi.getAllPosts().subscribe(posts => {
      this.posts = posts;
    });
  }
  deletePost(idPost: string) {
    const potvrda = confirm('Da li sigurno zelite da obrisete ovaj post?');
    if (potvrda) {
        this.dataApi.deletePost(idPost);
        console.log('Delete post', idPost);

      }

  }
  changePostModal(posts: NgForm) {
    this.dataApi.selectedPost = Object.assign({}, posts);
  }
}

