import { Component, OnInit } from '@angular/core';
import {BloggersService} from '../../services/bloggers.service';
import {BloggerInterface} from '../../interface/blogger'

@Component({
  selector: 'app-bloggers',
  templateUrl: './bloggers.component.html',
  styleUrls: ['./bloggers.component.css']
})
export class BloggersComponent implements OnInit {
public bloggers: BloggerInterface[];
  constructor(private bloggerService: BloggersService) { }

  ngOnInit() {
    this.getBloggers();
  }
getBloggers() {
  this.bloggerService.getAllBloggers().subscribe(bloggers => {
    this.bloggers = bloggers;
    console.log('Blogers', bloggers);
  });
}
}
