import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import {AuthService} from '../../../services/auth.service';
import {Router} from '@angular/router';
import {AngularFireStorage} from '@angular/fire/storage';
import {finalize} from 'rxjs/operators';
import { Observable } from 'rxjs/internal/Observable';
import { error } from '@angular/compiler/src/util';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  @ViewChild('imageUser') inputImageUser: ElementRef;
  public email: string = '';
  public password: string = '';
  uploadProcenat: Observable<number>;
  urlImage: Observable<string>;
  private filePath: string;

  constructor(private authService: AuthService, private router: Router, private storage: AngularFireStorage) { }

  ngOnInit() {

  }
  onUpload(e) {
    console.log('subir', e );
    const id = Math.random().toString(36).substring(2);
    const file = e.target.files[0];
    this.filePath = `upload/profilnaSlika_${id}`;
    const ref = this.storage.ref(this.filePath);
    const task = this.storage.upload(this.filePath, file);
    this.uploadProcenat = task.percentageChanges();
    task.snapshotChanges().pipe(finalize(() => this.urlImage = ref.getDownloadURL())).subscribe();

    console.log(this.uploadProcenat);
    console.log(this.urlImage);
  }
  onRegisterUser() {
    this.authService.registerUser(this.email, this.password)
    .then((res) => {
      this.authService.isAuth().subscribe(user => {
        if ( user ) {
          console.log('User je logovan', user);
          user.updateProfile({
            displayName : 'Goran ' ,
            photoURL: this.filePath
          }).then(() => {
            this.router.navigate(['home']);
            console.log('user updated');
          }).catch((err) =>  console.log('error', error));
        }
      });
    }).catch(err => console.log('err', err.message));
  }
  onLoginGoogle(): void {
    this.authService.loginGoogleUser()
    .then((res) => {
      console.log('resUser', res);
      this.OnLoginRoute();
    }).catch(err => console.log(err));

  }
  onLoginFb(): void {
this.authService.loginFacebookUser()
.then((res) => {
  console.log('resUser', res);
  this.OnLoginRoute();
  }).catch((err: any) => console.log(err));
}
OnLoginRoute() {
  this.router.navigate(['/home']);
}
}
