import { Injectable } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from '@angular/fire/firestore';
import { PostInterface } from '../interface/post';
import {HttpClient} from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';
import {map, mergeMap, retry, catchError, zip} from 'rxjs/operators';
import {from, forkJoin} from 'rxjs';
import {filter} from 'rxjs/operators';
import {BloggersService} from '../services/bloggers.service';
import { BloggerInterface } from '../interface/blogger';
import { database } from 'firebase';
import {ToastrService} from 'ngx-toastr';

@Injectable({
  providedIn: 'root'
})
export class DataApiService {
public date;
  constructor(private afs: AngularFirestore,
              private bloggerService: BloggersService,
              private toastr: ToastrService) { }

  private postsCollection: AngularFirestoreCollection<PostInterface>;
  private postCollection2;
  private posts: Observable<PostInterface[]>;
  private postDoc: AngularFirestoreDocument<PostInterface>;
  private post: Observable<PostInterface>;
  public selectedPost: PostInterface = {
    id : null
  };
  private posts2 = [];

  getAllPosts() {
     this.postsCollection = this.afs.collection<PostInterface>('posts');
     return this.posts = this.postsCollection.snapshotChanges()
      .pipe( map (changes => {
        return changes.map(action => {
          const data = action.payload.doc.data() as PostInterface;
          data.id = action.payload.doc.id;
          return data;
          });
      }));
  }
  getOnePost(idPost: string) {
    this.postDoc = this.afs.doc<PostInterface>(`posts/${idPost}`);
    return this.post = this.postDoc.snapshotChanges()
    .pipe( map (action => {
      if (action.payload.exists ===  false ) {
        return null;
      } else {
        const data = action.payload.data() as PostInterface;
        data.id = action.payload.id;
        return data;

      }
    }));
  }
  addPost(post: PostInterface) {
    this.postsCollection.add(post)
    .then(() => this.toastr.success('Add post'))
    .catch((err) => this.toastr.warning(err));
  }
  updatePost(post: PostInterface) {
    const idPost = post.id;
    this.postDoc = this.afs.doc<PostInterface>(`posts/${idPost}`);
    this.postDoc.update(post)
    .then(() => this.toastr.success('Edit Post'))
    .catch((err) => this.toastr.error(err));

    }

  deletePost(idPost: string) {
    this.postDoc = this.afs.doc<PostInterface>(`posts/${idPost}`);
    this.postDoc.delete()
    .then((succ) => this.toastr.warning('Deleted Posts'))
    .catch((err) => this.toastr.error(err));
  }
  getDate() {
    const today = new Date();
    const dd = String(today.getDate()).padStart(2, '0');
    const mm = String(today.getMonth() + 1).padStart(2, '0'); // January is 0!
    const yyyy = today.getFullYear();
    const today1 = mm + '/' + dd + '/' + yyyy;
    this.date = today1;
  }
  getPostBloggers() {
    const postBloggers = this.afs.collection('posts');
    return postBloggers.valueChanges().pipe(
      mergeMap((posts: PostInterface[]) =>
      this.afs.doc(`bloggers/${posts[0].userUID}`)
      .valueChanges().pipe(map(
        (bloggers2) => Object.assign({}, {posts,  bloggers2})
      )))

    );
  }
}
