import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './component/home/home.component';
import {Home1Component} from './component/home/home1/home1.component';
import { LoginComponent } from './component/users/login/login.component';
import { RegisterComponent } from './component/users/register/register.component';
import { ProfilComponent } from './component/users/profil/profil.component';
import { PageNotFoundComponent } from './component/page-not-found/page-not-found.component';
import { PostDetilsComponent } from './component/post-detils/post-detils.component';
import { ListPostsComponent } from './component/list-posts/list-posts.component';
import {AuthGuard} from './guards/auth.guard';
import { BloggersComponent } from './component/bloggers/bloggers.component';
const routes: Routes = [
  {path: '', component: Home1Component},
  {path: 'home' , component: HomeComponent},
  {path: 'bloggers', component: BloggersComponent},
  {path: 'user/login', component: LoginComponent},
  {path: 'user/register', component: RegisterComponent},
  {path: 'user/profil', component: ProfilComponent, canActivate: [AuthGuard]},
  {path: 'admin/list', component: ListPostsComponent, canActivate: [AuthGuard]},
  {path: 'post/:id', component: PostDetilsComponent, canActivate: [AuthGuard]},
  {path: '**', component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
