import { Injectable } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from '@angular/fire/firestore';
import { PostInterface } from '../interface/post';
import { Observable } from 'rxjs/internal/Observable';
import {map} from 'rxjs/operators';
import {BloggerInterface} from '../interface/blogger';
@Injectable({
  providedIn: 'root'
})
export class BloggersService {

  constructor(private afs: AngularFirestore) { }
private bloggerCollection: AngularFirestoreCollection<BloggerInterface>;
private bloggers: Observable<BloggerInterface[]>;
private bloggerDoc: AngularFirestoreDocument<BloggerInterface>;
private blogger: Observable<BloggerInterface>;
public currentBlogger: BloggerInterface = {
  id : null
};

  getAllBloggers() {
    this.bloggerCollection = this.afs.collection<BloggerInterface>('bloggers');
    return this.bloggers = this.bloggerCollection.snapshotChanges()
    .pipe( map (changes => {
       return changes.map(action => {
         const data = action.payload.doc.data() as BloggerInterface;
         data.id = action.payload.doc.id;
         return data;
         });
     }));
  }

  getOneBlogger(bloggerId: string) {
    this.bloggerDoc = this.afs.doc<BloggerInterface>(`bloggers/${bloggerId}`);
    return this.blogger = this.bloggerDoc.snapshotChanges()
    .pipe( map (action => {
        const data = action.payload.data() as BloggerInterface;
        data.id = action.payload.id;
        console.log('service blogger', data);
        return data;

      }
    ));
  }
  updateBlogger(blogger: BloggerInterface) {
    const idBlogger = blogger.id;
    this.bloggerDoc = this.afs.doc<BloggerInterface>(`bloggers/${idBlogger}`);
    this.bloggerDoc.update(blogger);
  }

}
