module.exports = "/**** Sass Variables ****/\r\n\r\n#profil{\r\n    background: url('20.jpg') no-repeat;\r\n    background-size: cover;\r\n    background-attachment: fixed;\r\n    margin-top: 20px;\r\n    min-height: 700px;\r\n  }\r\n\r\nbody {\r\n    font-family: 'Open Sans', sans-serif;\r\n    background: #333;\r\n  }\r\n\r\n.card-wrapper {\r\n    width: 400px;\r\n    height: 500px;\r\n    position: relative;\r\n  }\r\n\r\n.card {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    width: 350px;\r\n    height: 450px;\r\n    -webkit-transform: translate(-50%, -50%);\r\n            transform: translate(-50%, -50%);\r\n    border-radius: 16px;\r\n    overflow: hidden;\r\n    box-shadow: 0 5px 18px rgba(0, 0, 0, 0.6);\r\n    cursor: pointer;\r\n    transition: 0.5s;\r\n  }\r\n\r\n.card .card-image {\r\n    position: absolute;\r\n    top: 0px;\r\n    left: 0px;\r\n    width: 100%;\r\n    height: 100%;\r\n    z-index: 2;\r\n    background-color: #000;\r\n    transition: 0.5s;\r\n  }\r\n\r\n.card:hover img {\r\n    opacity: 0.4;\r\n    transition: 0.5s;\r\n  }\r\n\r\n.card:hover .card-image {\r\n    -webkit-transform: translateY(-100px);\r\n            transform: translateY(-100px);\r\n    transition: all 0.9s;\r\n  }\r\n\r\n/**** Social Icons *****/\r\n\r\n.social-icons {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    -webkit-transform: translate(-50%, -50%);\r\n            transform: translate(-50%, -50%);\r\n    z-index: 3;\r\n    display: flex;\r\n  }\r\n\r\n.social-icons li {\r\n    list-style: none;\r\n  }\r\n\r\n.social-icons li a {\r\n    position: relative;\r\n    display: block;\r\n    width: 50px;\r\n    height: 50px;\r\n    line-height: 50px;\r\n    text-align: center;\r\n    background: #fff;\r\n    font-size: 23px;\r\n    color: #333;\r\n    font-weight: bold;\r\n    margin: 0 6px;\r\n    transition: 0.4s;\r\n    -webkit-transform: translateY(200px);\r\n            transform: translateY(200px);\r\n    opacity: 0;\r\n  