import { Injectable } from '@angular/core';
import {AngularFireAuth} from '@angular/fire/auth';
import {map} from 'rxjs/operators';
import { auth } from 'firebase/app';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {BloggerInterface} from '../interface/blogger';
import {ToastrService} from 'ngx-toastr';
@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private afAuth: AngularFireAuth, private afs: AngularFirestore, private toastr: ToastrService ) { }

  registerUser(email: string, password: string) {
    return new Promise((resolve, reject) => {
      this.afAuth.auth.createUserWithEmailAndPassword(email, password)
      .then(userData => {
        resolve(userData),
        this.updateBloggerData(userData.user);
        this.toastr.success('You are registered');
      }).catch((error) => {
        console.log(reject(error));
        this.toastr.warning(error);
      });
    });
  }
  loginEmailUser(email: string , password: string) {
    return new Promise((resolve, reject) => {
    this.afAuth.auth.signInWithEmailAndPassword(email, password)
    .then(userData => resolve(userData),
      err => reject(err));
    });
  }
  loginFacebookUser() {
    return this.afAuth.auth.signInWithPopup(new auth.FacebookAuthProvider())
      .then((bloggerDetails => {
        this.updateBloggerData(bloggerDetails.user);
      }
        ));
  }

loginGoogleUser() {
  return this.afAuth.auth.signInWithPopup(new auth.GoogleAuthProvider())
    .then((bloggerDetails => this.updateBloggerData(bloggerDetails.user)));
}

  logOutUser() {
    return  this.afAuth.auth.signOut();
  }

  isAuth() {
    return this.afAuth.authState.pipe(map(auth => auth));
  }
  private updateBloggerData(blogger) {
    const bloggerRef: AngularFirestoreDocument<any> = this.afs.doc(`bloggers/${blogger.uid}`);
    console.log('podaci', blogger);
    const data: BloggerInterface = {
      name: blogger.displayName,
      photoURL: blogger.photoURL,
      id: blogger.uid,
      email: blogger.email
    };
    return bloggerRef.set(data, {merge: true});
  }
  isUserAdmin(bloggerID: string) {
    return this.afs.doc<BloggerInterface>(`bloggers/${bloggerID}`).valueChanges();
  }
}
