import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import {DataApiService} from '../../services/data-api.service';
import {BloggersService} from '../../services/bloggers.service';
import {BloggerInterface} from '../../interface/blogger';
import {PostInterface} from '../../interface/post';
import {PostsInterface} from '../../interface/posts';
import { Observable } from 'rxjs';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private dataApi: DataApiService, private bloggerService: BloggersService) { }
  public post = '';
  public posts;
  private bloggers: BloggerInterface[];
  public username: [];
  public userID: string;
  private blogger: BloggerInterface = {
    name: '',
    email: '',
    photoURL: '',
    facebook: '',
    twiter: '',
    roles: {}
  };
  private newPost: Observable<PostsInterface>;
  private newPosts;

  ngOnInit() {
   this.getAllBloggers();
   this.getAllPosts();
   this.dataApi.getPostBloggers().toPromise().then(
    posts => {this.newPosts = posts;
      console.log(this.newPosts);


     });

  }

  getAllPosts() {
    this.dataApi.getAllPosts().subscribe(posts => {
     console.log('POSTS', posts);
     this.posts = posts;
    });
  }
  getBlogger() {
    this.bloggerService.getOneBlogger('zb4lch4ndNPssHCiY1qs5EetXdb2').subscribe(blogger => {
      this.blogger = blogger;
      console.log('Blogger1', this.blogger);
      return blogger.name;
    });
  }
  getAllBloggers() {
    this.bloggerService.getAllBloggers().subscribe(data => {
      this.bloggers = data;
    });
  }

}
