import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { NgForm } from '@angular/forms';
import { BloggerInterface } from 'src/app/interface/blogger';
import { BloggersService } from '../../../../services/bloggers.service';

@Component({
  selector: 'app-modal-profil',
  templateUrl: './modal-profil.component.html',
  styleUrls: ['./modal-profil.component.css']
})
export class ModalProfilComponent implements OnInit {
  @Input() bloggerUID: string;
  @Input() blogger ;
  @ViewChild('btnCloseModal') btnClose: ElementRef;
  constructor(private bloggerService: BloggersService) { }

  ngOnInit() {
    console.log('modal', this.blogger);
  }
  onChangeBlogger(formBlogger: NgForm): void {
    console.log(formBlogger);
    console.log(this.blogger);
  }
  onSaveBlogger(formBlogger: NgForm): void {
    this.blogger = formBlogger.value;
    console.log(this.blogger);
    this.bloggerService.updateBlogger(this.blogger);
    this.btnClose.nativeElement.click();
  }
}
