import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../../services/auth.service';
import {BloggerInterface} from '../../../interface/blogger';
import { BloggersService} from '../../../services/bloggers.service';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'app-profil',
  templateUrl: './profil.component.html',
  styleUrls: ['./profil.component.css']
})
export class ProfilComponent implements OnInit {
blogger: BloggerInterface = {
  name: '',
  email: '',
  photoURL: '',
  facebook: '',
  twiter: '',
  roles: {}
};
public blogger1: BloggerInterface = {};
public providerId = null;
public bloggerUID = null;
  constructor(private authService: AuthService, private bloggerService: BloggersService) { }
  ngOnInit() {
    this.authService.isAuth().subscribe(blogger => {

      if (blogger) {
        this.blogger.name = blogger.displayName;
        this.blogger.email = blogger.email;
        this.blogger.photoURL = blogger.providerData[0].photoURL;
        this.providerId = blogger.providerData[0].providerId;
        this.bloggerUID = blogger.uid;
        this.getBlogger(this.bloggerUID);
      }

   });

  }
getBlogger(bloggerId: string) {
  this.bloggerService.getOneBlogger(bloggerId).subscribe(blogger => {
    this.blogger = blogger;
    console.log('Blogger1', this.blogger);
  });
}
  changeProfilModal(blogger: NgForm) {
    return this.blogger;
  }

}
